* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  width: 100%;
  background-color: skyblue;
}
.headroom {
  top: 0;
  left: 0;
  right: 0;
  Index: 1;
  background: #2ecc71;
}
.headroom--unfixed {
  position: relative;
  -webkit-transform: translateY(0);
          transform: translateY(0);
}
.headroom--scrolled {
  transition: -webkit-transform 200ms ease-in-out;
  transition: transform 200ms ease-in-out;
  transition: transform 200ms ease-in-out, -webkit-transform 200ms ease-in-out;
}
.headroom--unpinned {
  position: fixed;
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
}
.headroom--pinned {
  position: fixed;
  -webkit-transform: translateY(0%);
          transform: translateY(0%);
}
.input-group {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin-top: 5px;
  margin-bottom: 5px;
}
.login-btn {
  padding: 2px 30px;
   background-color: #417cef; 
  border: 0;
  font-size: 18px;
  border-radius: 3px;
  font-family: Oxygen, sans-serif;
  background-color: rgba(15, 15, 15, 0.03);
  margin-top: 20px;
  border: 2px solid #417cef;
  transition: background-color 0.3s ease-out;
  cursor: pointer;
}
.page-container {
  width: 800px;
  margin: 10px auto;
  padding: 32px;
  background-color: white;
  position: relative;
}

.product-listing {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1 1;
          flex: 1 1;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-flex-direction: row;
          flex-direction: row;
}

.product-listing .product-list-item {
  padding: 10px 30px;
  width: 200px;
}

ul.top-menu {
  position: absolute;
  right: 50px;
  top: 50px;
  margin: 0;
}

ul.top-menu li {
  display: inline-block;
}

ul.top-menu li:before {
  content: '|';
  margin: 0 4px;
}

ul.top-menu li:nth-child(1):before {
  content: '';
  margin: 0;
}
#root {
  width: 100%;
  height: 100%;
  box-sizing: border-box;

}

.root-container {
  width: 100%;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  
}

.box-controller {
  visibility: visible;
  display: -webkit-flex;
  display: flex;
  max-width: 24em;
  min-width: 21em;
  height: 40px;
  box-shadow: 0px 0px 20px 0px rgba(255, 14, 14, 0.2);
  margin-bottom: 30px;
  -webkit-align-items: center;
          align-items: center;
  transition: visibility 0.5s ease-out;
  background-color: #6565e7;
}

.controller {
  -webkit-flex: 1 1;
          flex: 1 1;
  text-align: center;
  height: 100%;
  line-height: 2;
  cursor: pointer;
  color: black;
 
  
}



.selected-controller {
  transition: border 0.5s ease-out;
  border-bottom: 2px solid orange;
}

.box-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  max-width: 24em;
  min-width: 21em;
  box-shadow: 0px 0px 20px 0px rgba(15, 15, 15, 0.2);
  border-radius: 6px;
  padding: 24px;
}

.inner-container {
  transition: visibility 0.2s ease-out;
}

.inner-container.show {
  visibility: visible;
}


.header {
  text-align: center;
  padding: 5px;
  margin-bottom: 17px;
  /* margin-left: 35px; */
  /* margin-right: 35px; */
  color: #2ecc71;
  font-family: Ozxgen, sans-serif;
  font-size: 22px;
  border-bottom: 2px solid blue_theme;
}

.box {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
}

.input-group {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin-top: 5px;
  margin-bottom: 5px;
}


.login-label {
  font-family: Oxygen, sans-serif;
  font-size: 20px;
  font-weight: 600;
  padding-left: 5px;
}


.login-input {
  height: 2.1em;
  border-radius: 3px;
  border: 1px solid blue_theme;
  transition: border 0.4s ease-out;
  padding: 13px;
  font-family: Oxygen, sans-serif;
  font-size: 19px;
  color: #3e3e42;
  background: rgba(15, 15, 15, 0.01);
}

.login-input:hover {
  border: 2px solid blue_theme;
}

.login-input:focus {
  border: 2px solid #6565e7;
  box-shadow: 0px 0px 20px rgba(15, 15, 15, 0.2);
}


.login-input::-webkit-input-placeholder {
  font-family: Oxygen, sans-serif;
  font-size: 16px;
  color: rgba(46, 213, 116, 0.839);
  color: rgba(15, 15, 15, 0.4);
}


.login-input::-ms-input-placeholder {
  font-family: Oxygen, sans-serif;
  font-size: 16px;
  color: rgba(46, 213, 116, 0.839);
  color: rgba(15, 15, 15, 0.4);
}


.login-input::placeholder {
  font-family: Oxygen, sans-serif;
  font-size: 16px;
  color: rgba(46, 213, 116, 0.839);
  color: rgba(15, 15, 15, 0.4);
}

.login-btn {
  padding: 2px 30px;
   background-color: #417cef; 
  border: 0;
  font-size: 18px;
  border-radius: 3px;
  font-family: Oxygen, sans-serif;
  background-color: rgba(15, 15, 15, 0.03);
  margin-top: 20px;
  border: 2px solid #417cef;
  transition: background-color 0.3s ease-out;
  cursor: pointer;
}

.login-btn:hover,
.login-btn:focus {
  background-color: #417cef;
  color: white;
}

.danger-error {
  color: #e74c3c;
  font-size: 16px;
}


.password-state {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  margin-top: 20px;
}

.pwd {
  height: 6px;
  -webkit-flex: 1 1;
          flex: 1 1;
  visibility: hidden;
}

.pwd-weak {
  background-color: #e74c3c;
}

.pwd-medium {
  background-color: #3498db;
}

.pwd-strong {
  background-color: #2ecc71;
}

.show {
  visibility: visible;
}
